import { render, screen } from '@testing-library/react';
import App from './App';

// Mock the date to ensure the app is unlocked for testing
const mockDate = new Date('2025-01-02T00:00:00');
jest.spyOn(global, 'Date').mockImplementation(() => mockDate);

test('renders birthday piano app', () => {
  render(<App />);
  
  // Check for the main birthday title
  const titleElement = screen.getByText(/HAPPY BIRTHDAY/i);
  expect(titleElement).toBeInTheDocument();
  
  // Check for the wifey message
  const wifeyElement = screen.getByText(/To My Beautiful Wifey/i);
  expect(wifeyElement).toBeInTheDocument();
  
  // Check for the start playing button
  const startButton = screen.getByText(/Start Playing/i);
  expect(startButton).toBeInTheDocument();
});

test('renders piano tiles game title', () => {
  render(<App />);
  
  const gameTitle = screen.getByText(/Falling Piano Tiles/i);
  expect(gameTitle).toBeInTheDocument();
});

test('renders love message', () => {
  render(<App />);
  
  const loveMessage = screen.getByText(/Made with love just for you/i);
  expect(loveMessage).toBeInTheDocument();
});
